import "core-js/modules/es.array.push.js";
import { ref } from '@vue/reactivity';
import { useRouter } from 'vue-router';
import store from '@/store';
export default {
  setup() {
    let router = useRouter();
    //头像
    let head = ref("");
    //名字
    let name = ref("邱智贤");
    let showthis = ref(true);
    //时间默认设定
    const currentDate = ref(new Date(2020, 0, 17));
    //时间的弹窗
    const show = ref(false);
    const show1 = ref(false);
    const show2 = ref(false);
    //获取手机号码
    let phone = ref(0);
    phone.value = store.state.phone;
    //学段
    let studylevel = ref("四级");
    //时间
    let thetime = ref("未设置");
    //性别
    const columns = ['男', '女', '保密'];
    const columns1 = ['四级', '六级', '考研', '专四', '专八'];
    let sex = ref("未设置");
    const showPopup = () => {
      show.value = true;
    };
    const showPopup1 = () => {
      show1.value = true;
    };
    const showPopup2 = () => {
      show2.value = true;
    };
    const afterRead = file => {
      // 此时可以自行将文件上传至服务器
      //获取到图片信息
      // console.log(file.content);
      head.value = file.content;
      //然后保存到本地
      window.localStorage.setItem("headimg", JSON.stringify(head.value));
    };
    //获取头像信息
    let headlocal = JSON.parse(window.localStorage.getItem("headimg"));
    //如果有的话,换头像
    if (headlocal && headlocal != undefined) {
      head.value = headlocal;
    }
    //改名字
    let changename = () => {
      // console.log("2");
      showthis.value = false;
    };
    //失去焦点事件
    let noon = () => {
      //  console.log(name.value);
      showthis.value = true;
    };
    //点击确定时间
    let daythis = e => {
      getSimpleDate(e);
      thetime.value = getSimpleDate(e).split(" ")[0];
      console.log(thetime.value);
      show.value = false;
    };
    //点击取消时间
    let cancels = () => {
      show.value = false;
    };
    //点击性别确定
    let onConfirm = value => {
      sex.value = value;
      show1.value = false;
    };
    //点击性别取消
    let onCancel = () => {
      show1.value = false;
    };
    //点击学段确定
    let onConfirm1 = value => {
      studylevel.value = value;
      show2.value = false;
    };
    //点击学段取消
    let onCancel1 = () => {
      show2.value = false;
    };

    //时间戳转换
    const getSimpleDate = date => {
      var y = date.getFullYear();
      var m = date.getMonth() + 1;
      m = m < 10 ? "0" + m : m;
      var d = date.getDate();
      d = d < 10 ? "0" + d : d;
      var h = date.getHours();
      var minute = date.getMinutes();
      minute = minute < 10 ? "0" + minute : minute;
      var s = date.getSeconds();
      s = s < 10 ? "0" + s : s;
      return y + "-" + m + "-" + d + " " + h + ":" + minute + ":" + s;
    };
    //点击保存信息
    let addmessage = () => {
      let messageData = {};
      messageData.name = name.value;
      messageData.thetime = thetime.value;
      messageData.sex = sex.value;
      messageData.studylevel = studylevel.value;
      //保存到本地
      window.localStorage.setItem("addmessage", JSON.stringify(messageData));
      //页面跳转
      router.push({
        path: "/my"
      });
    };
    //开始时读取信息
    let readmessage = () => {
      name.value = reader.name;
      thetime.value = reader.thetime;
      sex.value = reader.sex;
      studylevel.value = reader.studylevel;
    };
    let reader = JSON.parse(window.localStorage.getItem("addmessage"));
    if (reader) {
      readmessage();
    }
    let goBack = () => {
      router.go(-1);
    };
    return {
      sex,
      goBack,
      afterRead,
      head,
      changename,
      name,
      showthis,
      noon,
      minDate: new Date(2001, 1, 1),
      maxDate: new Date(2022, 12, 1),
      currentDate,
      show,
      showPopup,
      daythis,
      thetime,
      cancels,
      showPopup1,
      show1,
      showPopup2,
      show2,
      columns,
      columns1,
      onConfirm,
      onCancel,
      onConfirm1,
      onCancel1,
      studylevel,
      phone,
      addmessage
    };
  }
};